@import "normalaze";
@import "slick-theme.scss";
@import "slick.scss";

@mixin item {
    &__item{
        position: relative;
        &__hide{
            display: none;
        }
        img{
            width: 100%;
            height: 318px;
            object-fit: cover;
            border-radius: 3px;
        }
        &__price{
            background: $black;
            color: #fff;
            font-size: 14px;
            font-family: $lato;
            font-weight: 400;
            padding: 7px 17px;
            border-radius: 2px;
            position: absolute;
            left: 10px;
            bottom: 62px;
            text-decoration: none;
        }
        &__showitem{
            opacity: 0;
            padding: 7px 15px;
            border-radius: 2px;
            position: absolute;
            right: 9px;
            bottom: 51px;
            text-decoration: none;
            font-weight: 400;
            font-family: 'Lato', sans-serif;
            background: #ffab00;
            color: #fff;
        }
        &__hart{
            text-decoration: none;
            color: #9f9e9e;
            position: absolute;
            top: 8px;
            font-size: 33px;
            left: 12px;
        }
        &__hart:hover{
            color: red;
        }
        &__product{
            padding-top: 15px;
            display: flex;
            justify-content: space-between;
            h3{
                font-size: 16px;
                span{
                    display: block;
                    font-size: 14px;
                    color: #bdbdbd;

                }
            }
        
        &__stars{
            align-self: flex-end;
            a{
            text-decoration: none;
            color:#e0e0e0;
            }
            a:hover{
                color: $acient-color;
            }
        }
    }
}
}

@mixin button {
    text-decoration: none;
    color: $black;
    font-family: 'Lato', sans-serif;
}

@mixin title {
    &__content{
    text-align: center;
    padding-bottom: 41px;
   
    &__title{
     padding-bottom: 13px;
     font-size: 28px;
    }
    &__subtitle{
        font-size: 16px;
    }
}
}

$black:#333333;
$acient-color:#ffab00;
$lato: 'Lato', sans-serif;
$merry:'Merriweather', serif;

body{
    color: $black;

}


h2{
    font-family: 'Merriweather', serif;
    font-weight: 900;
    padding: 0;
    margin: 0;
}

h3{
    font-family: 'Merriweather', serif;
    font-weight: 400; 
    padding: 0;
    margin: 0;
}

p{
    font-family: 'Lato', sans-serif;
    font-size: 14px;
    font-weight: 400;
    padding: 0;
    margin: 0;
}

.container{
    max-width: 1140px;
    margin: 0 auto;
    padding-left: 10px;
    padding-right: 10px;
}


.page-header{
    &__content{
        display: flex;
        justify-content: space-between;
        padding-top: 11px;
        padding-bottom: 16px;
        align-items: center;
        @media (max-width:580px) {
            flex-direction: column;
        }
        &__login{
            i{
                font-size: 33px;
            }
            &__log{
                position: relative;
            }
            &__log::after{
                content: "";
                position: absolute;
                left: 42px;
                top: 2px;
                height: 100%;
                width: 1px;
                background: $black;
            }
            a{
                @include button();
                margin-left: 4px;
                font-weight: 700;
            }
            a:hover{
                color: $acient-color;
                
            }
            
        }
        &__logo{
            text-align: center;
            text-decoration: none;
            font-family:"Georgia", serif; 
            font-size: 46px;
            font-weight: bold;
            color: $black;
            @media (max-width:580px) {
                padding-top: 20px;
                padding-bottom: 20px;
            }
            span{
                display: block;
                @include button();
                font-size: 14px;
            }
        }
        &__list{
          
            a{
                text-decoration: none;
                color: $black;
                
            } 
            &__wish{
                margin-right: 22px;
                font-size: 34px;
            }
            &__cart{
                margin-right: 9px;
                font-size: 34px;
            }
            &__summ{
                font-size: 16px;
                font-weight: 700;
            }
        }
    }
    &__navigation{
        padding-top: 12px;
        position: relative;
        &__content{
            display: flex;
            justify-content: space-between;
            align-items: center;
            @media (max-width:600px) {
                flex-direction: column;
                justify-content: center;
            }
            &__search{
                text-decoration: none;
                color: $black;
                font-size: 24px;
                @media (max-width:450px) {
                    padding-top:20px;
                 }
            }
            &__catalog{
                display: flex;
                justify-content: space-between;
                @media (max-width:450px) {
                    flex-direction: column;
                    justify-content: center;
                }
                li{
                   list-style-type: none; 
                   margin-left: 11px;
                   @media (max-width:600px) {
                    margin-left: 5px;
                }
                @media (max-width:450px) {
                    margin-left: 0;
                    margin-top: 20px;
                }
             a{

                @include button();
                font-weight: 400;
                font-size: 16px;
                color: $black;
                padding: 3px 15px;
                transition: 0.2s ease-in-out;
             }
             a:hover{
                 background: $acient-color;
                 transition: 0.2s ease-in-out;
                 color: #fff;
             }
            }
        }
            &__social{
                @media (max-width:600px) {
                    padding-bottom:20px;
                 }
            a{
                text-decoration: none;
                font-size: 16px;
                color: $black;
                margin-left: 7px;
                @media (max-width:580px) {
                    font-size: 25px;
                    margin-left: 20px;  
                     
                }
               

            }
              
            }
        }
    }
    &__navigation::after{
        content: "";
        position: absolute;
        width: 100%;
        background: #bfbfbf;
        height: 1px;

    }
    &__navigation::before{
        content: "";
        position: absolute;
        width: 100%;
        background: #bfbfbf;
        height: 1px;

    }
}
.slick-next {
    right: 0; 
}
.slick-prev{
    right: 0;
}
.slick-slider {
    height: 560px;
}
.slick-dots li button:before {
    font-family: 'slick';
     font-size: 6px;
     line-height: 20px;
     position: absolute;
     top: 0;
     left: 0;
     width: 20px;
     height: 20px;
     content: '•'; /* <-- Вот этот символ */
     text-align: center;
     opacity: .25;
     color: #c5c5c5;
     -webkit-font-smoothing: antialiased;
     -moz-osx-font-smoothing: grayscale;
   }
   

