.page-email{
    padding-top: 46px;
    &__content{
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        @media (max-width:1024px) {
            flex-direction: column;
            justify-content: center;
        }
        &__text{
            font-family: $merry;
            font-weight: 300;
            font-size: 16px;
            font-style: italic;
        }
        input{
            outline: none;
            background: transparent;
            box-sizing: border-box;
            flex-basis: 31%;
            border: 1px solid $black;
            padding: 18px 21px;
            @media (max-width:1024px) {
                margin-top: 20px;
                margin-bottom: 20px;
            }
        }
        button{
            outline: none;
            border: none;
            background: $acient-color;
            color: #fff;
            font-size: 14px;
            font-family: $lato;
            font-weight: 700;
            height: 46px;
            flex-basis: 17%;
            text-transform: uppercase;
            cursor: pointer;
            transition: 0.3s ease-in-out;
            @media (max-width:1024px) {
                width: 191px;
                padding: 15px 0;
            }
        }
        button:hover{
            background: $black;
            color: $acient-color;
            transition: 0.3s ease-in-out;
        }
    }
    &__content::after{
        content: "";
        position: absolute;
        bottom: -26px;
        width: 100%;
        background: #bfbfbf;
        height: 1px;

    }
    &__content::before{
        content: "";
        position: absolute;
        top: -49px;
        width: 100%;
        background: #bfbfbf;
        height: 1px;

    }
}

