&__block{
   display: grid;
    grid-template-columns: 263px minmax(263px,848px);
    grid-auto-rows: max-content;
    grid-gap: 30px;
    padding-top: 50px;
    @media (max-width:600px) {
        grid-template-columns: 263px;
        justify-content: center;
    }

    &__search{
        position: relative;
        &__icon{
            position: absolute;
            right: 14px;
            top: 13px;
        }
        &__search{
            outline: none;
            border: 1px solid $black;
            padding: 12px;
            box-sizing: border-box;
            width: 100%;
        }
        &__by{
            position: relative;
            padding-top: 17px;
        }
        &__by::after{
            display: block;
            width: 100%;
            height: 1px;
            content: "";
            background: #d3d3d3;
            position: absolute;
            bottom: -16px;
        }
        &__item{
            padding-top: 26px;
            position: relative;
        &__categories{
       
            display: flex;
            justify-content: space-between;
            align-items: center;
            position: relative;
            p{
                font-size: 16px;
            }
           a{
               text-decoration: none;
               color: $black;
               transition: 0.2s ease-in-out;
            }

        
        }
        &__list{
          opacity: 0;
            display: none;
            li{
                list-style-type: none;
                margin-bottom: 18px;
                label{
                    font-size: 16px;
                    font-weight: 400;
                    font-family: $lato;
                    position: relative;
                    display: inline-block;
                    cursor: pointer;
                    padding-left: 11px;
                }
                label::before{
                    position: absolute;
                    left: -18px;
                    top: 2px;
                    background: #fff;
                    content: "";
                    width: 11px;
                    height: 11px;
                    border: 1px solid $black;
                    display: inline-block;
                }
                input{
                 cursor: pointer;
                 position: relative;
                 padding: 0;
                display: none;
                }
              
            }
        }
    
        
    }

    &__item::after{
        display: block;
        content: "";
        width: 100%;
        height: 1px;
        background: #d3d3d3;
    }
    }
    &__clotes{
        display: grid;
        grid-template-columns: repeat(auto-fit,262px);
        grid-auto-rows: max-content;
        justify-content: center;
        grid-gap: 30px;
        justify-content: center;
            @include item();
    
    }
}
}


