/* animation */

.page-mix__block__item__choose__active{
    opacity: 0.8;
    transition: 0.3s ease-in-out;
}

.rotate-icon{
    transform: rotate(180deg);
    transition: 0.2s ease-in-out;
}

.show-filter{
    opacity: 1;
display: block;
    animation: show-filterz 0.5s ease-in-out;
}

.show{
    display: block;
    transition: 0.3s ease-in-out;
    animation: showitems 0.4s;
}
.hide{
    display: none;
}
.shopnow{
    opacity: 100;
    animation:shopnows 0.2s;
    bottom: 61px;
}

@keyframes show-filterz{
    0%{
        transform: translateY(-150px);
      opacity: 0.2;
    }

    100%{
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes shopnows{
    0%{
        opacity: 0;
    }
    50%{
        opacity: 50;
        bottom: 56px;
    }
    100%{
        opacity: 100;
        bottom: 61px;
    }
}

@keyframes showitems{
    0%{
        opacity: 0;
    }
    50%{
        opacity: 50%;
    }
    100%{
        opacity: 100%;
    }
}